.page-container {
  background-color: #282c34;
}
.page-container--dark {
  background-color: white;
}

.App {
  text-align: center;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  font-size: 24px;
  color: #282c34;
  width: 1280px;
  margin: 0 auto;
  padding: 0 50px;
}
.App--dark {
  background-color: #282c34;
  color: white;
}
.App__header {
  margin-top: 40px;
}

.navigation {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 35px;
  font-size: 16px;
  border-bottom: 2px solid #282c34;
}
.navigation--dark {
  border-bottom-color: white;
}
.navigation--icon {
  height: 14px;
  width: 14px;
  padding: 7px 4px 4px 0;
}
.navigation--icon-switcher {
  height: 20px;
  width: 20px;
  padding: 0 5px 0 5px;
}
.navigation--icon-switcher--disactive {
  opacity: 25%;
}
.navigation--element {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: transform 0.5s;
}
.navigation--element:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.navigation--switcher {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: auto;
  gap: 10px;
  height: 30px;
  align-items: center;
  background-color: #282c34;
  border-radius: 10px;
  padding: 0 10px;
  transition: transform 0.5s;
}
.navigation--switcher--dark {
  background-color: white;
}
.navigation--switcher:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.navigation--link {
  text-decoration: none;
  color: #282c34;
}
.navigation--link:hover {
  cursor: pointer;
}
.navigation--link--dark {
  text-decoration: none;
  color: white;
}
.navigation--link--disactive {
  opacity: 25%;
}
.navigation--switcher-text {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  color: #282c34;
}
.navigation--switcher-text:hover {
  cursor: pointer;
}
.navigation--switcher-text--dark {
  text-decoration: none;
  color: white;
}
.navigation--switcher-text--disactive {
  opacity: 25%;
}

.header {
  display: flex;
  flex-direction: column;
}
.header__photo {
  margin-top: 30px;
}

.photo {
  border-radius: 50%;
  height: 330px;
  width: 330px;
  overflow: hidden;
}
.photo--img {
  max-width: 100%;
  height: auto;
}

.main--text {
  font-size: 20px;
  margin: 0 auto;
}
.main p {
  margin-block-start: 0.7em;
  margin-block-end: 0.7em;
}
.main__footer {
  margin-top: 35px;
  margin-bottom: 10px;
}

.title {
  display: flex;
  align-items: center;
  cursor: pointer;
  border: 1px solid transparent;
}
.title--line {
  flex: 1;
  margin: 0 20px;
}
.title:hover {
  border: 1px solid #282c34;
  border-radius: 15px;
}
.title--dark:hover {
  border-color: white;
}

.about-me-container {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
}
.about-me-container--show {
  height: 200px;
}

.skills-container {
  display: flex;
  justify-content: space-between;
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
  text-align: left;
}
.skills-container ul {
  flex: 1;
}
.skills-container--show {
  height: 240px;
}

.portfolio {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
}
.portfolio--show {
  height: 550px;
}
.portfolio--title {
  font-weight: bold;
  font-size: 24px;
}
.portfolio__title:not(:first-child) {
  margin-top: 50px;
}

.experience {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
  text-align: left;
}
.experience--show {
  height: 1450px;
}
.experience--position {
  display: flex;
  justify-content: space-between;
}
.experience__position:not(:first-child) {
  margin-top: 50px;
}
.experience--company {
  font-weight: bold;
  font-size: 23px;
}
.experience p {
  margin-block-start: 0.4em;
  margin-block-end: 0.4em;
}

.education {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
}
.education--show {
  height: 150px;
}
.education--title {
  font-weight: bold;
  font-size: 23px;
}

.aditional-education {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
}
.aditional-education--show {
  height: 130px;
}

.achievement {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
}
.achievement--show {
  height: 150px;
}

.link {
  color: #282c34;
}
.link--dark {
  color: white;
}

.footer {
  font-size: 12px;
}

