.page-container {
  background-color: #282c34;;

  &--dark {
    background-color: white;
  }
}

.App {
  text-align: center;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  font-size: 24px;
  color: #282c34;
  width: 1280px;
  margin: 0 auto;
  padding: 0 50px;

  &--dark {
    background-color: #282c34;
    color: white;
  }

  &__header {
    margin-top: 40px;
  }
}

.navigation {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 35px;
  font-size: 16px;
  border-bottom: 2px solid #282c34;

  &--dark {
    border-bottom-color: white;
  }

  &--icon {
    height: 14px;
    width: 14px;
    padding: 7px 4px 4px 0;
  }

  &--icon-switcher {
    height: 20px;
    width: 20px;
    padding: 0 5px 0 5px;

    &--disactive {
      opacity: 25%;
    }
  }

  &--element {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    transition: transform 0.5s;

    &:hover {
      transform: scale(1.1);
      cursor: pointer;
    }
  }

  &--switcher {

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: auto;
    gap: 10px;
    height: 30px;
    align-items: center;
    background-color: #282c34;
    border-radius: 10px;
    padding: 0 10px;
    transition: transform 0.5s;

    &--dark {
      background-color: white;
    }

    &:hover {
      transform: scale(1.1);
      cursor: pointer;
    }
  }

  &--link {
    text-decoration: none;
    color: #282c34;

    &:hover {
      cursor: pointer;
    }

    &--dark {
      text-decoration: none;
      color: white;
    }

    &--disactive {
      opacity: 25%;
    }
  }

  &--switcher-text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
    color: #282c34;

    &:hover {
      cursor: pointer;
    }

    &--dark {
      text-decoration: none;
      color: white;
    }

    &--disactive {
      opacity: 25%;
    }
  }
}

.header {
  display: flex;
  flex-direction: column;

  &__photo {
    margin-top: 30px;
  }
}

.photo {
  border-radius: 50%;
  height: 330px;
  width: 330px;
  overflow: hidden;

  &--img {
    max-width: 100%;
    height: auto;
  }
}

.main {
  &--text {
    font-size: 20px;
    margin: 0 auto;
  }

  & p {
    margin-block-start: 0.7em;
    margin-block-end: 0.7em;
  }

  &__footer {
    margin-top: 35px;
    margin-bottom: 10px;
  }
}

.title {
  display: flex;
  align-items: center;
  cursor: pointer;
  border: 1px solid transparent;

  &--line {
    flex: 1;
    margin: 0 20px;
  }

  &:hover {
    border: 1px solid #282c34;
    border-radius: 15px;
  }

  &--dark:hover {
    border-color: white;
  }
}

.about-me-container {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;

  &--show {
    height: 200px;
  }
}

.skills-container {
  display: flex;
  justify-content: space-between;
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
  text-align: left;

  & ul {
    flex: 1;
  }

  &--show {
    height: 240px;
  }
}

.portfolio {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;

  &--show {
    height: 550px;
  }

  &--title {
    font-weight: bold;
    font-size: 24px;
  }

  &__title:not(:first-child) {
    margin-top: 50px;
  }
}

.experience {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
  text-align: left;

  &--show {
    height: 1450px;
  }

  &--position {
    display: flex;
    justify-content: space-between;
  }

  &__position:not(:first-child) {
    margin-top: 50px;
  }

  &--company {
    font-weight: bold;
    font-size: 23px;
  }

  & p {
    margin-block-start: 0.4em;
    margin-block-end: 0.4em;
  }
}

.education {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;

  &--show {
    height: 150px;
  }

  &--title {
    font-weight: bold;
    font-size: 23px;
  }
}

.aditional-education {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;

  &--show {
    height: 130px;
  }
}

.achievement {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;

  &--show {
    height: 150px;
  }
}

.link {
  color: #282c34;

  &--dark {
    color: white;
  }
}

.footer {
  font-size: 12px;
}